import React, { Component } from "react";

class ProductionLabelsForm extends Component {
  render() {
    return (
      <div>
        <div class="to-filter-bg">
          <form class="dropdownCenter">
            <div class="form-row ">
              <div class="form-group col-md-3 ">
                <label class="es_inputDropdownLebel">Ship Week</label>
                <input type="text" class="form-control" placeholder="15/2019" />
              </div>
              <div class="form-group col-md-3">
                <label class="es_inputDropdownLebel">Sow Week</label>
                <input type="text" class="form-control" placeholder="31/2019" />
              </div>
              <div class="form-group col-md-3">
                <label class="es_inputDropdownLebel">Batch</label>
                <input type="text" class="form-control" placeholder="1" />
              </div>
              <div class="form-group col-md-3">
                <label class="es_inputDropdownLebel">Item ID</label>
                <input type="text" class="form-control" placeholder="223344" />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductionLabelsForm;
