import React, { Component } from "react";
import { Link } from "react-router-dom";

import Logout from "./Logout";

class LoginArea extends Component {
  state = {
    bgScale: true
  };

  ChangeScale = () => {
    this.setState({
      bgScale: false
    });
  };
  render() {
    const { bgScale } = this.state;
    return (
      <div>
        <section className={bgScale ? "loginArea" : "loginArea bg-scale"}>
          <div className="container">
            <div className="row">
              <div className="col-xl-12">
                <div className="loginHeroImage text-center">
                  <img src={require("../assets/images/vbm_hero_img_2.png")} />
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <div className="loginForm">
                  <form action="">
                    <div className="form-group">
                      <input
                        onClick={this.ChangeScale}
                        type="text"
                        className="form-control"
                        placeholder="Employee ID"
                      />
                    </div>
                    <div className="form-group">
                      <input
                        type="password"
                        className="form-control"
                        id=""
                        placeholder="6 Digit Pin"
                      />
                    </div>
                  </form>
                  <p>Hint: This is your hint</p>
                  <div className="loginFormButtonLogin text-center">
                    <span>
                      <a className="">
                        <Link to="/dashboard">Login </Link>
                      </a>
                    </span>
                  </div>

                  <div className="loginFormButtonFoget text-center">
                    <span>
                      <a
                        href=""
                        data-toggle="modal"
                        data-target="#forgetPin"
                        className="forgetPinButton"
                      >
                        <Link to="/">Forget PIN </Link>
                      </a>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <div className="loginPagefooterText text-center">
                  <p>v 1.0</p>
                  <p>Copyright &copy; IDAPT 2019</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <Logout />
      </div>
    );
  }
}

export default LoginArea;
