import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "../../components/Header";
import DirtProductionMain3Filter from "./Components/DirtProductionMain3Filter";

class DirtProductionMain3 extends Component {
  render() {
    return (
      <div>
        <Header pageTitle="Dirt Production" headerSetting="to-hide" />
        <DirtProductionMain3Filter />

        <div className="row">
          <div className="container">
            <div className="col-xl-12">
              <div className="text-center es_verticaCenter">
                <h1 className="es_dirtText">Please choose an option</h1>

                <section className="button-area">
                  <div className="text-center">
                    <div className="BottomButtons">
                      <Link to="/seeding/bale_busting">
                        {" "}
                        <button type="button" className="btn to-button-default">
                          Bale Busting
                        </button>
                      </Link>

                      <Link to="/seeding/dirt_production2">
                        {" "}
                        <button type="button" className="btn to-button-default">
                          Tray Filling
                        </button>
                      </Link>
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DirtProductionMain3;
