import React, { Component } from "react";
import "../../../App.css";
class DirtProductionUnallocated3Buttons extends Component {
  render() {
    return (
      <div>
        <section class="button-area">
          <div class="text-center">
            <div className="BottomButtons">
              <button type="button" class="btn  to-button-disabled" disabled>
                Hold
              </button>
              <button type="button" class="btn   to-button-default">
                Add Note
              </button>
              <button type="button" class="btn   to-button-disabled" disabled>
                Edit
              </button>
              <button
                onclick="location.href='seed-production-main.html';"
                type="button"
                class="btn  to-button-default"
              >
                Save
              </button>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default DirtProductionUnallocated3Buttons;
